}

#jersery {
    padding-top: 25px;
    padding-left: 50px;
}

#pawery{
    color: navy;
    text-align: center;

}

#ctp{
    display: block;
    margin-right: auto;
    width: 100%;

}

#daycard{
    color: navy;
    text-align: center;
}




